import AOS from 'aos';
import Swiper from 'swiper';
import SwiperCore, { Navigation, Pagination, EffectFade, Thumbs } from 'swiper/core';

AOS.init();

SwiperCore.use([Navigation, Pagination, EffectFade, Thumbs]);

const swiperThumbs = new Swiper('.swiper-right', {
  direction: 'horizontal',
  breakpoints: {
    1024: {
      direction: 'vertical',
      slidesPerView: 6,
      spaceBetween: 0
    },
    480: {
      slidesPerView: 3,
      spaceBetween: 30
    },
    420: {
      slidesPerView: 2,
    }
  },
  slidesPerView: 'auto',
  pagination: {
    el: '.pagination-fractions',
    type: 'fraction'
  },
  freeMode: true,
  watchSlidesVisibility: true,
  watchSlidesProgress: true
});

const swiperImages = new Swiper('.swiper-middle', {
  effect: 'fade',
  fadeEffect: {
    crossFade: true
  },
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev",
  },
  pagination: {
    el: ".pagination-dots",
    clickable: true,
  },
  thumbs: {
    swiper: swiperThumbs,
  },
});

swiperImages.on('slideChange', () => {
  const currentSlideElement = document.querySelector('.slide-current');
  currentSlideElement.textContent = swiperImages.activeIndex < 10 ? `0${swiperImages.activeIndex + 1}` : `${swiperImages.activeIndex + 1}`;
})

